@keyframes tooltip {
  0% {
    transform: translateX(-50%) scale(0);
    opacity: 0;
  }
  50% {
    transform: translateX(-50%) scale(0.99);
  }
  to {
    transform: translateX(-50%) scale(1);
    opacity: 1;
    visibility: visible;
  }
}
@keyframes tooltip-horizontal {
  0% {
    transform: translateY(-50%) scale(0);
    opacity: 0;
  }
  50% {
