  padding: 0.625rem;
  text-align: center;
  white-space: nowrap;
  display: inline-block;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  top: 0px;
}
.vib-container .vib-thumbnail-wrapper .vib-thumbnail {
  background-position: center;
  background-size: cover;
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.2);
  cursor: pointer;
  display: inline-block;
  height: 50px;
  margin: 2px 2px 0 2px;
  overflow: hidden;
  width: 50px;
}
.vib-container .vib-thumbnail-wrapper .vib-thumbnail-active {
  background-position: center;
  background-size: cover;
  border-radius: 4px;
  box-shadow: inset 0 0 0 2px white;
  cursor: pointer;
  display: inline-block;
  height: 50px;
  margin: 2px 2px 0 2px;
  overflow: hidden;
  width: 50px;
}
.vib-container .vib-footer {
  color: white;
  background: rgba(0, 0, 0, 0.65);
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
